'use client'

import { useForm } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import * as z from 'zod'
import { Button } from '@/components/ui/button'
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form'
import { Input } from '@/components/ui/input'
import { Textarea } from '@/components/ui/textarea'
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select'
import { useState } from 'react'
import { ImageUpload } from '@/components/ui/image-upload'

// Base schema for all facility types
const baseSchema = z.object({
  name: z.string().min(1, 'Name is required'),
  description: z.string().min(1, 'Description is required'),
  images: z.array(z.string()).min(1, 'At least one image is required'),
})

// Type-specific schemas
const facilitySchemas = {
  'individual-cabin': baseSchema.extend({
    totalCabins: z.number().min(1, 'Must have at least 1 cabin'),
    availableCabins: z.number().min(0, 'Cannot be negative'),
    pricePerMonth: z.number().min(0, 'Price cannot be negative'),
  }),
  'coworking-spaces': baseSchema.extend({
    totalSeats: z.number().min(1, 'Must have at least 1 seat'),
    availableSeats: z.number().min(0, 'Cannot be negative'),
    pricePerDay: z.number().min(0, 'Price cannot be negative'),
  }),
  'meeting-rooms': baseSchema.extend({
    capacity: z.number().min(1, 'Must have at least 1 person capacity'),
    pricePerHour: z.number().min(0, 'Price cannot be negative'),
    amenities: z.array(z.string()),
  }),
  // Add schemas for other facility types...
}

type FacilityFormProps = {
  type: string
  onSubmit: (data: any) => void
}

export function FacilityForm({ type, onSubmit }: FacilityFormProps) {
  const [images, setImages] = useState<string[]>([])
  const schema = facilitySchemas[type as keyof typeof facilitySchemas] || baseSchema

  const form = useForm<z.infer<typeof schema>>({
    resolver: zodResolver(schema),
    defaultValues: {
      name: '',
      description: '',
      images: [],
    },
  })

  const handleSubmit = (values: z.infer<typeof schema>) => {
    onSubmit({ ...values, images })
  }

  const renderTypeSpecificFields = () => {
    switch (type) {
      case 'individual-cabin':
        return (
          <>
            <FormField
              control={form.control}
              name="totalCabins"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Total Cabins</FormLabel>
                  <FormControl>
                    <Input type="number" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <FormField
              control={form.control}
              name="availableCabins"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Available Cabins</FormLabel>
                  <FormControl>
                    <Input type="number" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <FormField
              control={form.control}
              name="pricePerMonth"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Price per Month (â‚¹)</FormLabel>
                  <FormControl>
                    <Input type="number" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
          </>
        )
      // Add cases for other facility types...
      default:
        return null
    }
  }

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(handleSubmit)} className="space-y-6">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <FormField
            control={form.control}
            name="name"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Facility Name</FormLabel>
                <FormControl>
                  <Input {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="description"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Description</FormLabel>
                <FormControl>
                  <Textarea {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          {renderTypeSpecificFields()}
        </div>

        <div className="space-y-4">
          <FormLabel>Facility Images</FormLabel>
          <ImageUpload
            value={images}
            onChange={(urls) => {
              setImages(urls)
              form.setValue('images', urls)
            }}
            onRemove={(url) => {
              const newImages = images.filter((image) => image !== url)
              setImages(newImages)
              form.setValue('images', newImages)
            }}
          />
        </div>

        <div className="flex justify-end">
          <Button type="submit" className="w-full md:w-auto">
            Add Facility
          </Button>
        </div>
      </form>
    </Form>
  )
} 